<template>
    <div class="addAddress_container">
        <head-top head-title="添加地址" goBack="true"></head-top>
        <section class="addAddress_container_add"> 
            <section class="addAddress_list">
                <span class="section_left">联系人</span>
                <section class="section_right">
                    <input type="text" placeholder="你的名字" class="input_style" v-model="name"/>
                    <div class="shoose_xe">
                        <span class="choose_option">
                            <svg class="address_empty_right" @click="chooseSex(1)" :class="{choosed: sex == 1}">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                            </svg>
                            <span>先生</span>
                        </span>
                        <span class="choose_option">
                            <svg class="address_empty_right" @click="chooseSex(2)" :class="{choosed: sex == 2}">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                            </svg>
                            <span>女士</span>
                        </span>
                    </div>
                </section>
            </section>
            <section class="addAddress_list">
                <span class="section_left">手机号码</span>
                <section class="section_right">
                    <div class="phone_add">
                        <input type="text" placeholder="手机号码" class="input_style" v-model="phone"/>
                        <img src="../../../../images/add_phone.png" height="20" width="20" @click="phone_bk = true">    
                    </div>
                    <input type="text" placeholder="备用电话" class="input_style" v-model="anntherPhoneNumber" v-show="phone_bk"/>
                </section>
            </section>
            <section class="addAddress_list">
                <span class="section_left">送餐地址</span>
                <section class="section_right">
                    <router-link tag="div" to="/confirmOrder/chooseAddress/addAddress/searchAddress" class="choose_address" >{{searchAddress? searchAddress.name : '小区/写字楼/学校等'}}</router-link>
                    <input type="text" placeholder="详细地址(如门牌号等)" v-model="address_detail" class="input_style">
                </section>
            </section>
            <section class="addAddress_list">
                <span class="section_left">标签</span>
                <section class="section_right">
                    <input type="text" placeholder="无/家/学习/公司" v-model="tag" class="input_style">
                </section>
            </section>
        </section>
        <div class="button_tyle" @click="addAddress">确定</div>
        <alert-tip v-if="showAlert" @closeTip="showAlert=false" :alertText="alertText"></alert-tip>
        <transition>
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
import headTop from '../../../../components/header/header'
import {mapState,mapMutations} from 'vuex'
import {getAddress,getUser,postAddAddress} from '../../../../service/getData'
import alertTip from '../../../../components/common/alertTip'
export default {
    components: {
        headTop,
        alertTip
    },created () {
      
    },mounted () {
       
    },data(){
        return{
            name:'',//姓名
            sex:1,//性别
            phone:'',//电话
            address_detail:null,//详细地址
            tag:'',//备注
            tag_type:1,//备注类型
            phone_bk:false,//是否选择备注电话
            anntherPhoneNumber:'',//备注电话
            showAlert:false,//弹出框
            alertText:'',//弹出框信息
            poi_type:0//地址类型
        }
    },computed: {
        ...mapState([
            'userInfo','geohash','searchAddress'
        ])
    },methods: {
        ...mapMutations([
            'CONFIRM_ADDRESS'
        ]),
        //选择性别
        chooseSex(id)
        {
            this.sex=id;
        },
        //保存地址信息
        async addAddress()
        {
            if(!this.userInfo)
            {
                this.showAlert=true;
                this.alertText="请先登陆";
                return;
            }else if(!this.name)
            {
                this.showAlert=true;
                this.alertText="请输入姓名";
                return;
            }else if(!this.phone)
            {
                this.showAlert=true;
                this.alertText="请输入电话号码";
                return;
            }else if(!this.searchAddress)
            {
                this.showAlert=true;
                this.alertText="请选择地址";
                return;
            }else if(!this.address_detail)
            {
                this.showAlert=true;
                this.alertText="请选择详细地址";
                return;
            }
             if (this.tag == '家') {
                    this.tag_type = 2;
            }else if(this.tag == '学校'){
                    this.tag_type = 3;
            }else if(this.tag == '公司'){
                    this.tag_type = 4;
            }
            let res=await postAddAddress(this.userInfo.user_id,this.searchAddress.name,this.address_detail,this.geohash,this.name,this.phone,this.anntherPhoneNumber,0,this.sex,this.tag,this.tag_type);
            if(res.message)
            {
                this.showAlert=true;
                this.alertText=res.message;
            }else{
                //确认添加新的地址
                this.CONFIRM_ADDRESS(1);
                this.$router.go(-1);
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../../../style/mixin.scss';
.addAddress_container{
    position: fixed;
    top:0;
    left:0;
    bottom:0;
    right:0;
    background: #f1f1f1;
    z-index: 220;
}
.addAddress_container_add{
    background: #fff;
    margin-top:2rem;
    }
.addAddress_list{
    display: flex;
    padding:0.6rem;
    border-bottom: 0.025rem solid #f1f1f1;
    .section_left{
          align-items: center;
        flex: 2;
        @include sc(0.7rem,#666);
        font-weight: bold;
        line-height: 2.5rem;
    }
    .section_right{
        flex:5;
        align-items: center;
        .input_style{
            border-bottom:0.025rem solid $bc;
            @include sc(0.7rem,#666);
            height: 2.5rem;
            width: 100%;
        }
    }
}
.shoose_xe{
  display: flex;
  line-height: 2.5rem;
  border-bottom:0.025rem solid #f1f1f1;
  .choose_option{
      @include sc(0.7rem,#666);
      display: flex;
      margin-right: 0.8rem;
       align-items: center;
      svg{
          @include wh(.8rem,.8rem);
          margin-right: .3rem;
          fill:#ccc;
      }
      .choosed{
          fill:#4cd964;
      }
  }
}
.phone_add{
    display: flex;
    align-items: center;
}
.choose_address{
     border-bottom: 0.025rem solid #f5f5f5;
    line-height: 2.5rem;
    @include sc(0.7rem,#999);
}
.button_tyle{
    background-color: #4cd964;
    text-align: center;
    line-height: 1.8rem;
    margin:0 0.7rem;
    margin-top:0.6rem;
    border-radius: 0.2rem;
    @include sc(0.7rem,#fff);
}
</style>



